<template>
  <div
    v-loading="loading"
    class="side-tree"
  >
    <gs-search
      v-if="!isEmpty"
      ref="search"
      v-bind="searchProps"
      v-model="searchVal"
      placeholder="请输入目录名称"
    />
    <gs-tree
      v-if="!isEmpty"
      ref="tree"
      :render-content="renderTreeNode"
      v-bind="treeProps"
      @node-toggle="handleNodeToggle"
    />
    <gs-button
      v-if="!loading && isEmpty"
      type="primary"
      @click="createCategory"
    >点击添加目录</gs-button>
  </div>
</template>

<script>
export default {
  props: {
    loading: {
      type: Boolean,
      default: false
    },
    searchProps: {
      type: Object,
      default() {
        return {};
      }
    },
    treeProps: {
      type: Object,
      default() {
        return {};
      }
    },
    keyword: {
      type: String,
      default: ''
    },
    actions: {
      type: [Array, Function],
      default() {
        return [];
      }
    },
    active: {
      type: Object,
      default() {
        return {};
      }
    }
  },

  data() {
    return {
      searchVal: this.keyword
    };
  },

  computed: {
    isEmpty() {
      return this.loading || this.treeProps.data.length === 0;
    }
  },

  watch: {
    keyword(val) {
      this.searchVal = val;
    },
    searchVal(val) {
      this.$emit('search', val);
    }
  },

  methods: {
    renderTreeNode(h, {node, data}) {
      let {
        actions,
        treeProps,
        active
      } = this;
      const {
        label,
        value,
        deleting = 'deleting'
      } = treeProps.props;
      let itemClass = 'side-tree-node-label';

      if (typeof actions === 'function') {
        actions = actions(data);
      }
      itemClass += active[value] === data[value] ? ' is-active' : '';
      itemClass += data[deleting] ? ' is-deleting' : '';
      return (
        <div class="side-tree-node-content">
          {data[deleting] ? (
            <span class={itemClass}>
              <gs-icon
                class="side-tree-node-icon"
                title="正在删除"
                name="loading"
              />
              {data[label]}
            </span>
          ) : (
            <span
              title={data[label]}
              class={itemClass}
              on-click={this.handleClick.bind(this, data)}
            >
              <gs-icon
                class="side-tree-node-icon"
                name=" iconfont icon-wenjianjia1"
              />
              {data[label]}
            </span>
          )}

          <gs-dropdown
            class="side-tree-action"
            trigger="click"
            on-select={this.handleAction.bind(this, data)}
          >
            <gs-icon
              class="gs-dropdown-link"
              name="ellipsis"
              mode="button"
            />
            <gs-dropdown-menu align="right" slot="dropdown">
              <gs-dropdown-items options={actions} />
            </gs-dropdown-menu>
          </gs-dropdown>
        </div>
      );
    },

    handleNodeToggle(node, isToggle) {
      this.$emit('node-toggle', node.data, isToggle);
    },

    handleClick(data) {
      this.$emit('node-click', data);
    },

    handleAction(data, value) {
      this.$emit('node-action', data, value);
    },

    createCategory() {
      this.$emit('create-category');
    }
  }
};
</script>




