<template>
  <gs-popover
    ref="popover"
    v-bind="popoverProps"
    v-model="visible"
    class="action-comfirm-popover"
    supernatant
    placement="top"
    trigger="click"
  >
    <div class="action-comfirm">
      <slot>
        <gs-icon
          class="action-comfirm-tip-icon"
          name="exclamation-circle"
        />{{ title }}
      </slot>
    </div>
    <div class="action-comfirm-footer">
      <gs-button
        type="text-primary"
        @click="onComfirm"
      >确定</gs-button>
      <gs-button
        type="text"
        @click="onCancel"
      >取消</gs-button>
    </div>
    <slot
      slot="reference"
      name="reference"
    />
  </gs-popover>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      default: ''
    },
    popoverProps: {
      type: Object,
      default() {
        return {};
      }
    }
  },

  data() {
    return {
      visible: false
    };
  },

  methods: {
    onComfirm() {
      this.visible = false;
      this.$emit('comfirm');
    },

    onCancel() {
      this.visible = false;
      this.$emit('cancel');
    }
  }
};
</script>



