<template>
  <div class="two-column-layout">
    <div class="left-col">
      <slot name="left" />
    </div>
    <div class="right-col">
      <slot name="right" />
    </div>
  </div>
</template>

<script>
export default {
  props: {
    leftProps: {
      type: Object,
      default() {
        return {
          span: 6,
          xs: {
            span: 24
          }
        };
      }
    },
    rightProps: {
      type: Object,
      default() {
        return {
          span: 18,
          xs: {
            span: 24
          }
        };
      }
    }
  }
};
</script>




