<template>
  <div class="add-relation-box">
    <gs-button
      type="primary"
      @click="showModal"
    >添加关联</gs-button>
    <gs-modal
      v-model="visible"
      :width="800"
      class="add-relation-modal"
      title="添加关联表"
    >
      <gs-row>
        <gs-col
          v-bind="leftSpan"
          class="relation-col"
        >
          <label class="relation-label">待添加</label>
          <gs-search
            :value="keyword"
            :placeholder="searchType.label === 0 ? '请输入数据库' : '请输入数据表'"
            @search="handleSearch"
          >
            <template slot="prepend">
              <gs-select v-model="searchType.label">
                <gs-option
                  v-for="item in searchTypes"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                />
              </gs-select>
            </template>
          </gs-search>
          <div
            class="database-box"
            @scroll="handleScroll"
          >
            <gs-tree
              v-if="!isEmpty"
              ref="tree"
              :default-checked-keys="defaultChckedRelations"
              :data="treeData"
              node-key="value"
              show-checkbox
              @current-change="handleSelectChange"
            />
            <div
              v-show="loading"
              class="database-loading"
            >
              <gs-icon name="loading" />加载中
            </div>
            <div
              v-show="isEmpty && !loading"
              class="database-empty"
            >
              暂无数据
            </div>
          </div>
        </gs-col>
        <gs-col
          v-bind="rightSpan"
          class="relation-col"
        >
          <label class="relation-label">已选表 ({{ selectedTables.length }})</label>
          <el-table
            :data="selectedTables"
            max-height="300"
          >
            <el-table-column
              prop="label"
              label="数据表"
            />
            <el-table-column
              prop="databaseName"
              label="所属数据库"
            />
            <el-table-column
              prop="value"
              label="操作"
              width="80"
            >
              <template slot-scope="scope">
                <gs-icon
                  name="close"
                  mode="button"
                  @click="removeTable(scope.row)"
                />
              </template>
            </el-table-column>
          </el-table>
        </gs-col>
      </gs-row>
      <template slot="footer">
        <gs-button
          :loading="isCreating"
          type="primary"
          @click="handleConfirm"
        >确定</gs-button>
        <gs-button
          :disabled="isCreating"
          @click="close"
        >取消</gs-button>
      </template>
    </gs-modal>
  </div>
</template>

<script>
import {
  mapState,
  mapActions
} from 'vuex';
import {
  checkLoading
} from 'src/store/status';

export default {
  props: {
    categoryGuid: {
      type: String,
      default: ''
    }
  },

  data() {
    return {
      visible: false,
      leftSpan: {
        span: 8,
        xs: {
          span: 24
        }
      },
      rightSpan: {
        span: 15,
        offset: 1,
        xs: {
          span: 24,
          offset: 0
        }
      },
      keyword: '',
      searchType: {
        current: 0,
        label: 0
      },
      searchTypes: [
        {
          value: 0,
          label: '库'
        },
        {
          value: 1,
          label: '表'
        }
      ],
      page: 1,
      pageSize: 15,
      selectedNodes: []
    };
  },

  computed: {
    ...mapState('metadata', {
      metadatas: 'metadatas',
      tables: 'tables'
    }),
    ...mapState('relation', {
      currentAllRelations: 'currentAllRelations',
      relationsStatus: 'relationsStatus',
      createStatus: 'createStatus'
    }),
    defaultChckedRelations() {
      return this.currentAllRelations.list.map(item => item.tableGuid);
    },
    metadataList() {
      const list = this.metadatas.list || [];
      const defaultChckedRelations = this.defaultChckedRelations;

      return list.map(item => {
        let disabled = true;
        const children = item.children.map(item => {
          const temp = item.disabled || defaultChckedRelations.indexOf(item.value) !== -1;

          if (!temp) {
            disabled = false;
          }
          return {
            ...item,
            disabled: temp
          };
        });

        return {
          ...item,
          disabled,
          children
        };
      });
    },
    tableList() {
      const list = this.tables.list || [];
      const defaultChckedRelations = this.defaultChckedRelations;

      return list.map(item => {
        const temp = item.disabled || defaultChckedRelations.indexOf(item.value) !== -1;

        return {
          ...item,
          disabled: temp
        };
      });
    },
    treeData() {
      // 只有加载完全部关联列表，才显示对应的tree列表
      if (checkLoading(this.relationsStatus)) {
        return [];
      }
      return this.searchType.current === 0 ? this.metadataList : this.tableList;
    },
    loading() {
      if (this.searchType.current === 0) {
        return checkLoading(this.metadatas.status);
      } else {
        return checkLoading(this.tables.status);
      }
    },
    isEmpty() {
      return this.treeData.length === 0;
    },
    isCreating() {
      return checkLoading(this.createStatus);
    },
    selectedTables() {
      return this.selectedNodes.map(item => item.data);
    }
  },

  watch: {
    visible(val) {
      this.keyword = '';
      this.searchType = {
        current: 0,
        label: 0
      };
      this.selectedNodes = [];
      if (val) {
        this.page = 1;
        this.getAllRelations({
          categoryGuid: this.categoryGuid
        });
        this.queryMetadatas();
      }
    }
  },

  methods: {
    ...mapActions('relation', [
      'createRelation',
      'getAllRelations'
    ]),
    ...mapActions('metadata', [
      'getMetadatas',
      'getTableList'
    ]),

    showModal() {
      this.visible = true;
    },

    async queryMetadatas() {
      const {
        keyword,
        page,
        pageSize,
        metadatas,
        tables
      } = this;
      const total = this.searchType.current === 0 ? metadatas.total : tables.total;
      const offset = (page - 1) * pageSize;

      if (offset >= total && total > 0) {
        return;
      }

      const params = {
        query: keyword,
        offset,
        limit: pageSize
      };
      if (this.searchType.current === 0) {
        await this.getMetadatas(params);
      } else {
        await this.getTableList(params);
      }
      this.initSelect(this.treeData, this.defaultChckedRelations, this.selectedNodes.map(item => {
        return item.data.value;
      }));
    },
    /**
     * 初始化默认选中情况
     */
    initSelect(tree, defaultChckedRelations, selectedArr) {
      tree.forEach(item => {
        if (defaultChckedRelations.indexOf(item.value) !== -1) {
          // 组件库disabled不能设置选中，所以先设置为false，选中后，再还原true
          item.disabled = false;
          this.$refs.tree.setChecked(item.value, true);
          item.disabled = true;
        }
        if (selectedArr.indexOf(item.value) !== -1) {
          this.$refs.tree.setChecked(item.value, true);
        }
        if (item.children) {
          this.initSelect(item.children, defaultChckedRelations, selectedArr);
        }
      });
    },

    handleSearch(value) {
      this.searchType.current = this.searchType.label;
      this.keyword = value;
      this.page = 1;
      this.queryMetadatas();
    },

    handleScroll(e) {
      const distance = 20;
      const wrapper = e.target;
      const height = wrapper.offsetHeight;
      const top = wrapper.scrollTop;
      const scrollHeight = wrapper.scrollHeight;

      if (scrollHeight - height - top <= distance && !this.loading) {
        this.page += 1;
        this.queryMetadatas();
      }
    },

    findIndex(value) {
      const selectedTables = this.selectedTables;
      let index = -1;

      for (let i = 0; i < selectedTables.length; i++) {
        if (selectedTables[i].value === value) {
          index = i;
          break;
        }
      }

      return index;
    },

    handleSelectChange(node, checked) {
      const data = node.data;
      const selectedArr = this.selectedNodes.map(item => {
        return item.data.value;
      });
      // 默认选中的选项和已经选中的直接忽略
      if (checked && (this.defaultChckedRelations.indexOf(data.value) !== -1 || selectedArr.indexOf(data.value) !== -1)) {
        return;
      }

      let index = this.findIndex(data.value);

      if (checked) {
        this.selectedNodes.push(node);
      } else {
        this.selectedNodes.splice(index, 1);
      }
    },

    removeTable(row) {
      let index = this.findIndex(row.value);
      const node = this.selectedNodes[index];
      // 判断当前tree中是否有该节点
      if (this.$refs.tree.getCheckedKeys().indexOf(node.$node.key) !== -1) {
        this.$refs.tree.setChecked(node.$node.key, false);
      } else {
        this.selectedNodes.splice(index, 1);
      }
    },

    handleConfirm() {
      const guids = this.selectedTables.map(item => {
        return {
          tableGuid: item.tableGuid,
          tableName: item.tableName,
          dbName: item.dbName,
          status: item.status
        };
      });

      if (guids.length === 0) {
        this.$Message.warning('请选择要关联的表');
        return;
      }
      this.createRelation({
        guids,
        categoryGuid: this.categoryGuid
      }).then(() => {
        this.$Message.success('添加关联成功');
        this.$emit('create-success');
        this.close();
      });
    },

    close() {
      this.visible = false;
    }
  }
};
</script>




