<template>
  <gs-modal
    v-model="visible"
    :title="title"
    class="category-ceate-modal"
  >
    <gs-form
      ref="form"
      :model="formData"
      :rules="rules"
      label-width="70px"
    >
      <gs-form-item
        label="目录名称"
        prop="name"
      >
        <gs-input
          v-model="formData.name"
          placeholder="请输入目录名称，最大长度64个字符"
        />
      </gs-form-item>
      <gs-form-item
        label="描述"
        prop="description"
      >
        <gs-textarea
          v-model="formData.description"
          placeholder="请输入描述信息，最大长度20个字符"
        />
      </gs-form-item>
    </gs-form>
    <template slot="footer">
      <gs-button
        :loading="loading"
        type="primary"
        @click="handleOk"
      >确定</gs-button>
      <gs-button
        :disabled="loading"
        @click="setVisible(false)"
      >取消</gs-button>
    </template>
  </gs-modal>
</template>

<script>
import {
  mapState,
  mapActions
} from 'vuex';
import {
  checkLoading
} from 'src/store/status';

export default {
  props: {
    isShow: {
      type: Boolean,
      default: false
    },
    type: {
      type: Number,
      default: 1
    },
    category: {
      type: Object,
      default() {
        return {};
      }
    }
  },

  data() {
    return {
      rules: {
        name: [
          {
            required: true,
            max: 64,
            message: '目录名称不能为空且不能大于64个字符'
          }
        ]
      },
      visible: this.isShow,
      formData: this.category
    };
  },

  computed: {
    ...mapState('category', {
      createStatus: 'categoryCreateStatus',
      updateStatus: 'categoryUpdateStatus'
    }),

    loading() {
      return checkLoading(this.createStatus) || checkLoading(this.updateStatus);
    },

    title() {
      let title = '';
      switch (this.type) {
        case 0:
          title = '在前添加一级目录';
          break;
        case 1:
          title = '在后添加一级目录';
          break;
        case 2:
          title = '添加子目录';
          break;
        case 3:
          title = '编辑目录';
          break;
      };
      return title;
    }
  },

  watch: {
    isShow(val) {
      this.visible = val;
    },

    category(val) {
      this.formData = val;
    },

    visible(val) {
      // 重现显示清空之前的验证信息
      val && this.$refs.form.resetFields();
      this.$emit('update:isShow', val);
    }
  },

  methods: {
    ...mapActions('category', [
      'createCategory',
      'updateCategory'
    ]),

    setVisible(visible) {
      this.visible = visible;
    },

    handleOk() {
      this.$refs.form.validate(isValid => {
        if (isValid) {
          const {
            type,
            formData
          } = this;
          let promise;
          let message = '添加成功';
          if (type === 0 || type === 1 || type === 2) {
            promise = this.createCategory(formData);
          }
          if (type === 3) {
            promise = this.updateCategory(formData);
            message = '修改成功';
          }
          promise.then(() => {
            this.$Message.success(message);
            this.setVisible(false);
          });
        }
      });
    }
  }
};
</script>



